* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.state {
  max-width: 640px;
  width: calc(100% - 20px);
  margin: 0 auto;
  border: 2px solid #333;
  padding: 30px;
}
.state h1 {text-align: center;}
.state input {
  display: block;
  width: 100%;
  height: 40px;
  border: 1px solid #333;
  padding: 10px;

}
.state button {
  width: 100%;
  height: 30px;
  margin-top: 4px;
  border: 1px solid #333;
}
.state p {
  border: 1px solid #333;
  margin-top: 20px;
  padding: 10px;
}
.state p span {
  display: block;
}